
.taskItem{
    background-color: #00000078;
    border-bottom: 1px solid #ffffff;
    border-top: 1px solid #ffffff;
    box-sizing: border-box;
    margin-bottom: 5px;
    display: flex;
    flex-direction: column;
    position: relative;
}
.taskList{
    background-color: antiquewhite;
}
.listItem{
    width: 100%;
    max-width: 500px;
    margin: auto;
}
.taskType{
    display: block;
    color: #f64215;
    font-weight: 700;
    font-family: sans-serif;
    background-color: #0a5315;
    text-shadow: 1px 1px 3px #000000;
}   
.taskSubItem{
    background-color: #ecad10;
    border-bottom-left-radius: 10px;
    border-top: 10px solid #293c00;
    border-bottom-right-radius: 10px;
    margin-left: 6px;
    padding: 3px;
    width: -moz-fit-content;
    /* width: fit-content; */
    display: inline-block;
}

.inputTaskSubItem{
    margin-top: 3px;
    display: block;
    width: 40%;
    max-width: 130px;
    
}
.inputDescription{
    display: block;
}
.subWorkContainer{
    border-bottom: 1px solid black;
    border-left: 1px solid black;
    background-color: #8383832e;
    padding-bottom: 4px;
    width: fit-content;
    padding-left: 10px;
    margin-left: 5px;
    margin-top: 5px;
}
.sideTitle{
    display: flex;
    justify-content: start;
}

.taskDate{
    background-color: black;
    position: -webkit-sticky;
    align-self: center;
    position: sticky;
    color: #ffffff;
    top: 102px;
    z-index: 1;
    max-width: 400px;
    width: 58%;
    text-align: center;
    border-bottom-right-radius: 20px;
    border-bottom-left-radius: 20px;
    box-shadow: 0px 3px 6px -1px #0a0c0c;
}
.taskCost{
    background-color: gold;
    margin-top: 5px;
    display: block;
    width: fit-content;
    padding: 1px;
}   
.taskName{
    background-color: #293c00;
    display: table;
    margin-top: 3px;
    font-family: sans-serif;
    color: #ffffff;
    text-shadow: 1px 2px 2px black;
    padding: 6px;
    border-radius: 5px;
}
.liItem{
    box-shadow: 0px 0px 3px 0px;
    margin: 10px 10px;
    padding: 3px;
    background-color: #5d7b13;
}
.taskCost,.liItem{
    border-radius: 4px;
}
.mark{
    color: white;
    text-shadow: 0px 0px 3px #000000;
}
.titleContainer{
    display: flex;
    justify-content: space-around;
    margin: 0 auto;
    width: 95%;
    border: 1px outset #ffffff2e;
    padding: 3px 0;
    border-top-left-radius: 30px;
    border-top-right-radius: 30px;
    background-color: #5d7b12;
    box-shadow: 0px 1px 4px 0px;
    margin-top: 9px;
}
.subWork{
    display: flex;
}

.btnChange{
    background-color: #d2681e;
    width: 15%;
    overflow: hidden;
    text-overflow: ellipsis;
    padding: 1px;
    border: 1px solid greenyellow;
    border-top-right-radius: 20px;
}


/* ///////////////////////// */



.dateItemWeek{
    margin-top: 5px;
    margin-bottom: 5px;
}
.weekWeek{
    display: flex;
    justify-content: space-between;
    border: 1px solid darkorange;
    width: 90%;
    margin: 0 auto;
}
.dayWeek{
    position: relative;
    border-left: 1px solid indianred;
    padding: 0 1.2vw;
    width: 100%;
    background-color: #ffffff;
}

.checkedDay{
    background-color: #4fc14d;
}
.todayDay:before{
    content: '';
    font-weight: bold;
    color: #ffffff;
    width: 10px;
    height: 10px;
    background-color: #af2e2e;
    position: absolute;
    display: block;
    right: 2px;
    top: 4px;
    z-index: 10;
    border-radius: 50%;
}

.dayWeek:first-child{
    border: 0;
}

.navBar{
    display: flex;
    justify-content: space-around;
    margin: auto;
    padding: 8px;
    position: sticky;
    top: 52px;
    z-index: 20;
    background: linear-gradient(45deg, #4088c600 4%, #fbbb00 53% , #4088c600 94%);
}
.navBarChecked{
    outline: 0;
    background-color: red !important;
}
.navBarLink{
    
}
.navBarLink > button.btn  {
    padding: 5px 3vw !important;
}